
<template>
	<div id="loginContains">
		<div class="loginImage">
			<img src="../assets/image/timg.jpg" alt="" />
		</div>
		<div class="formContains">	
			<div class="group">
				<span class="iconfont icon-shoujihao fl"></span>
				<input type="number" class="fl" placeholder="请输入手机号" v-model="account"/>
				<div class="fr phone code tc getcode" ref="getcodeMain">
					<a href="javascript:;" @click="getCode()" ref="getcode">获取验证码</a>
				</div>
			</div>	
			<div class="group">
				<span class="iconfont icon-iconfontmima1 fl"></span>
				<input type="text" class="fl" placeholder="请输入图片验证码" v-model="pictureCode"/>
				<div class="code fr" @click="refreshCode">
				    <s-identify :identifyCode="identifyCode"></s-identify>
				</div>	
			</div>	
			 <div class="group">
				<span class="iconfont icon-iconfontmima1 fl"></span>
				<input type="number" class="fl" placeholder="请输入验证码" v-model="VerificationCode"/>				
			</div>	
			
		</div>
		<a href="javascript:;" id="loginBtn" class="tc" @click="login">登录</a>
	</div>
    
</template>

<script>
	var countdown=60;
	var codeTF=0;
	//生成图片验证码
import Vue from 'vue'
import SIdentify from '@/components/identify'
import { MessageBox } from 'mint-ui';
Vue.use(SIdentify)
export default {
  name: "codetest",
  data() {
    return {
	  account:'',//手机号
	  pictureCode:'',//图片验证码
	  VerificationCode:'',//验证码
      identifyCodes: "1234567890abcdefghigklmnopqrstuvwxyzABCDEFGHIGKLMNOPQRSTUVWXYZ", //默认生成验证码的数字、字符
      identifyCode: "" ,//最终生成验证码的结果
      
    };
  },
  mounted() {
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
  },
  methods: {
  	//生成随机数
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },
    //点击验证码时内容替换
    refreshCode() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    //制作验证码 第一个参数是默认的字符，第二个参数是几位字符
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[
          this.randomNum(0, this.identifyCodes.length)
        ];
      }
      console.log(this.identifyCode);
    },
    //验证码倒计时函数 
    settime(time) {  
    	var that=this;
      if (countdown == 0) {  
        this.$refs.getcodeMain.style.background="#E6706E"; 
        this.$refs.getcodeMain.style.pointerEvents="auto";//启动点击事件
        this.$refs.getcode.innerText="获取验证码";
        countdown = 60;  
        return false;  
      } else {  
      	this.$refs.getcodeMain.style.background="#eee"; 
      	this.$refs.getcodeMain.style.pointerEvents="none";//禁止点击事件
      	this.$refs.getcode.innerText=countdown+'s';
        countdown--; 
      }  
       setTimeout(function(){
            that.settime(60) 
        },1000)
    },
    //获取验证码
    getCode(){
    	 if(!(/^1[34578]\d{9}$/.test(this.account))){ 
	        MessageBox('提示', '手机号输入不正确');
	        return false; 
	    }else{
	    	this.settime(60); 
	    	 MessageBox('提示', '请求接口获取手机验证码');
	    }
    },
    login(){
    	//判断图片验证码是否正确
    	if(this.pictureCode==this.identifyCode){
    		codeTF=1;
    	}else{
    		codeTF=0;
    	}
    	if(this.account!=''&&codeTF==1&&this.VerificationCode!=''){
    		MessageBox('提示', '请求接口');
    		this.$router.go(-1);
    		window.localStorage.setItem("account",this.account);
    	}else{
    		MessageBox('提示', '请输入正确的手机号，验证码、图片验证码');
    	}
    }
  },
  components:{
		SIdentify:SIdentify
	}
};
</script>

<style scoped="scoped" lang="less">
#loginContains{
	width:100%;
	.loginImage{
		width: 100%;
		height: 4.08rem;
		img{
			width: 100%;
			height: 100%;
		}
	}
	.formContains{
		padding:0.3rem;
		.group{
			height: 0.8rem;
		    border-bottom: 0.02rem solid #eee;
		    padding-bottom: 0.03rem;
		    margin-bottom:0.2rem;
			span{
				font-size: 0.48rem;
			    color: #4a4a4a;
			    margin-right: 0.2rem;
			    line-height: 0.8rem;
			}
			input{
				width:3.5rem;
				border:none;
				height: 100%;
			}
			.phone{
				line-height: 0.8rem;
				background: #A71D5D;
				a{
					color: #fff;
				}
			}
			.code {
			  width: 2.28rem;
			  height: 100%;			 
			}
		}
		
	}
	#loginBtn{
			width:100%;
			height: 1rem;
			background: #E6706E;
			position: fixed;
			bottom: 0;
			color: #fff;
			line-height: 1rem;
		}
}
/**/
</style>



