<template>
	<div>
		<div id="meContains">		
			<input type="file" name="file" @change="uploadFile"/>
			<img :src="imgurl" :alt="imgurl" />
			<mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
			<mt-field label="邮箱" placeholder="请输入邮箱" type="email" v-model="email"></mt-field>
			<mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
			<mt-field label="手机号" placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
			<mt-field label="网站" placeholder="请输入网址" type="url" v-model="website"></mt-field>
			<mt-field label="数字" placeholder="请输入数字" type="number" v-model="number"></mt-field>
			<mt-field label="生日" placeholder="请输入生日" type="date" v-model="birthday"></mt-field>
			<mt-field label="自我介绍" placeholder="自我介绍" type="textarea" rows="4" v-model="introduction"></mt-field>
			<mt-switch v-model="values">开关{{values+valuenum}}</mt-switch>
			<br />
			<mt-button type="primary" @click="getValue">提交</mt-button>
			<p>{{value}}</p>
		</div>
		<tabbar></tabbar>
	</div>
	  
</template>

<script>
	import Vue from 'vue'
	import Tabbar from '@/components/tabbar'
	import { Field } from 'mint-ui';
	Vue.component(Field.name, Field);
	import { Button } from 'mint-ui';
	Vue.component(Button.name, Button);
	import { Switch } from 'mint-ui';
	Vue.component(Switch.name, Switch);
export default {
  name: '',
  data () {
    return {
      username:'',
      email:"",
      password:"",
      phone:'',
      website:"",
      number:"",
      birthday:"",
      introduction:"",
      value:"",
      //开关 0 是关，1是开
      values:false,
      valuenum:0,
      //图片路径
      imgurl:""

    }
  },
  methods:{
  	getValue(){
  		this.value='用户名：'+this.username+'邮箱：'+this.email+'密码：'+this.Password+'手机号：'+this.phone+'网址：'+this.website+'数字：'+this.number
  		+'生日：'+this.birthday+'l留言：'+this.introduction
  	},
  	uploadFile(e){
//		console.log(e.target.files[0],e.target.files[0].name,e.target.files[0].type);//图片对象，2.图片路径，3、图片类型
  		var formData = new FormData(); 
        formData.append('file', e.target.files[0]);
        formData.append('type', 'test');
//      console.log(formData)
        this.imgurl=e.target.files[0].name
  	}
  },
  components:{
  	tabbar:Tabbar
  },
  mounted(){
		//页面加载的时候判断是不是登录状态，没有登录前往登录页面
		var account=window.localStorage.getItem("account");
		if(account==null||account==""){
			this.$router.push({path:"/login"})
		}
	}
}
</script>

<style>
	#meContains{
		padding-bottom: 1rem;
	}
	img{
		display: block;
		border:0.05rem solid #eee;
		width:2rem;
		height: 2rem;
	}
</style>


