<template>
	<div id="tabbar">
		
		<mt-tabbar>
		  <mt-tab-item id="首页">
			  	<router-link to="/home">
			  		<i class="iconfont icon-shouye"></i>
			   		首页		  		
			  	</router-link>
		   
		  </mt-tab-item>
		  <mt-tab-item id="列表">
			  	<router-link to="/list">
				   <i class="iconfont icon-ziliaoneirongguanli_huaban"></i> 
				    列表
			     </router-link>
		  </mt-tab-item>
		 
		  <mt-tab-item id="购物车">
			   	<router-link to="shoppingCart">
				   <i class="iconfont icon-gouwuche"></i>
				    购物车
			     </router-link>
		  </mt-tab-item>
		  <mt-tab-item id="我的">
			   <router-link to="me">
			   <i class="iconfont icon-wode"></i>
			    我的
			     </router-link>
		  </mt-tab-item>
		</mt-tabbar>
		
	</div>
	

</template>

<script>
	import Vue from 'vue'
	import { Tabbar, TabItem } from 'mint-ui';
	Vue.component(Tabbar.name, Tabbar);
	Vue.component(TabItem.name, TabItem);
	export default{
		
	}
</script>

<style scoped lang="less">
	/*单独组件里这么引*/
	/*@import "../assets/font/iconfont.css"*/
	#tabbar{
		position: fixed;
		bottom: 0;
		width: 100%;
		height: 1.12rem;
		
	}
		.mint-tab-item-label .iconfont{
			display: block;
			font-size: 0.4rem;
			margin-bottom: 0.2rem;
		}
		.router-link-active{
			color: #DF5000;
		}
	
	
</style>


